.topLeftAlign {
  position: relative;
  line-height: 150%;
}
.topLeftAlignParent {
  align-self: stretch;
  height: 21.33px;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: space-between;
}
.frameParent,
.simpleText6 {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.frameParent {
  align-self: stretch;
  flex: 1;
  justify-content: space-between;
}
.simpleText6 {
  position: relative;
  background-color: #fff;
  width: 100%;
  height: 1024px;
  overflow: hidden;
  padding: 20px;
  box-sizing: border-box;
  justify-content: flex-start;
  text-align: left;
  font-size: var(--font-size-sm);
  color: var(--color-black);
  font-family: var(--font-inter);
}
